<template>
  <div class="home">
    <!-- <div id="calc-wrapper" class="hidden"></div> -->
    <div
      class="word-wrapper"
      @touchstart="touchStart"
      @touchmove="touchMove"
      @touchend="touchEnd"
      @contextmenu="preventContextMenu"
      @click="cancelSelect"
      ref="content-wrapper"
    >
      <div ref="dynamic-div-wrapper">
        <div
          class="selection"
          v-for="(pos, index) in selectedDivList"
          :key="`selection-${index}`"
          :style="{left:`${pos.left}px`, top:`${pos.top}px`, width:`${pos.width}px`,height:`${pos.height}px`}"
        ></div>
      </div>

      <div ref="line-div-wrapper">
        <div
          :class="['underline',
            {
              's0': divItem.class === 'straight',
              's1': divItem.class === 'mark',
              's2': divItem.class === 'break',
              'bg1': divItem.bg === 'bg1',
              'bg2': divItem.bg === 'bg2'
            }
          ]"
          v-for="(divItem, index) in drawDivList"
          :key="`underline-${index}`"
          :style="{left:`${divItem.left}px`, top:`${divItem.top}px`, width:`${divItem.width}px`, height:`${divItem.height}px`}"
          @click.stop="divItem.fun(divItem.key)"
        ></div>
      </div>

      <div class="cursor-wrapper">
        <span
          class="cursor-left"
          v-if="cursorLeftPos"
          :style="{left:`${cursorLeftPos.left}px`, top:`${cursorLeftPos.top}px`}"
          @touchmove.stop="handleCursorLeftTouchMove"
          @touchend="handleCursorLeftTouchEnd"
        ></span>
        <span
          class="cursor-right"
          v-if="cursorRightPos"
          :style="{left:`${cursorRightPos.left}px`, top:`${cursorRightPos.top}px`}"
          @touchmove.stop="handleCursorRightTouchMove"
          @touchend="handleCursorRightTouchEnd"
        ></span>
      </div>
      <div v-show="isShowToolbar" class="toolbar-wrapper" :style="{left:`${toolbarPos.left}px`, top:`${toolbarPos.top}px`}" ref="toolbar">
        <button class="btn" @click="copyText">复制</button>
        <button class="btn" @click="drawLine('straight')">直线</button>
        <button class="btn" @click="drawLine('break')">波浪线</button>
        <button class="btn" @click="drawLine('mark')">马克笔</button>
        <button v-if="currentParagraphKey" class="btn" @click="delLine">删除划线</button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
const ROW_HEIGHT = 23 // 行高
const ROW_GAP = 5 // 行间距
const IMG_GAP = 10 // 图片的上下间隔
const CURSOR_WIDTH = 20 // 小游标的宽度
let cancelable = false // 点击是否可取消选择

export default {
  name: 'Home',
  data() {
    return {
      text: `[插图]NBA香港运营有限公司（“我们”）委托深圳市腾讯计算机系统有限公司(“腾讯”
      运营和维护NBA中国官方应用软件（“NBA中国APP”）和其他与NBA主题相关的网站、应用和服务。
      我们非常重视用户的隐私和个人信息保护。本《NBA APP隐私政策》（以下称“本隐私政策”或“本政策”）
      与您所使用的NBA APP以及我们运营的其他与NBA APP主题相关的网站、应用服务以及该等服务所包括的各种业务功能（
      以下统称“我们的产品和/或服务”）息息相关，我们希望通过向您说明我们在您使用我们的产品和/或服务时如何收集、使用、存
      储、共享和转让这些信息，以及我们为您提供的访问、更新、删除和保护这些信息的方式。其中，我们在“儿童个人信息保护”章节作了对于14周岁以下未成年人
      （以下称“儿童”）个人信息保护的单独说明。需要特别[插图][插图]说明的是，本政策不适用于其他第三方向您提供的服务，第三方向您提供的服务适用其向您说明的隐私权政策。
      请您在使用我们的产品和/或服务前仔细阅读并确认您已经充分理解本政策所写明的内容，并按照本隐私政策的指引作出您认为适当的选择。为了使您充分理解
      本政策的内容，本政策中与您的权益存在或可能存在重大关系的条款，我们已采用粗体字、斜体和/或下划线进行标注提示您注意。您使用或在我们更新本隐私
      政策后（我们会及时在相关平台上更新）继续使用我们的产品和/或服务，即意味着[插图]您同意本隐私政策（含更新版本）内容，并且同意我们按照本隐私政策收集、
      使用、存储和共享您的相关信息。如对本政策内容有任何疑问、意见或建议，您可通过发送邮件至NBAChinaAPP@nba.com的方式与我们联系。[插图]`,
      imgList: [
        {
          url: 'https://img03.sogoucdn.com/app/a/07/e58d89131f3a0882b804313208e0e983',
          width: 300,
          height: 200
        },
        {
          url: 'http://img5.imgtn.bdimg.com/it/u=1577269115,2062955874&fm=26&gp=0.jpg',
          width: 300,
          height: 200
        },
        {
          url: 'http://img3.imgtn.bdimg.com/it/u=1592339910,2937105685&fm=26&gp=0.jpg',
          width: 300,
          height: 200
        },
        {
          url: 'http://www.xialingying.tv/Uploads/Editor/20170928/1506562176622220.jpg',
          width: 837,
          height: 528
        },
        {
          url: 'http://1804.img.pp.sohu.com.cn/images/blog/2012/9/19/17/4/e34106171_13aa1becb3dg86_blog.jpg',
          width: 607,
          height: 431
        }
      ],
      // ↓↓↓↓↓↓↓↓↓↓↓↓↓↓↓ 以下为 positiomMap 的数据结构
      // {
      //   123: [    123为每行的top值
      //     {
      //       left: 22,
      //       width: 16
      //     }
      //   ]
      // }
      selectedText: '', // 当前选中段落的文本
      currentParagraphKey: null, // 当前选中段落的key值
      isShowToolbar: false, // 是否展示工具栏
      toolbarPos: {}, // 工具栏位置
      cursorLeftPos: null, // 左边游标实时位置
      cursorRightPos: null, // 右边游标实时位置
      positionMap: {}, // 每个字的位置信息
      selectedDivList: [], // 手指选中过的div列表
      drawDivObj: {} // 所有划线段落
    }
  },
  computed: {
    drawDivList() { // 最终要划线的div信息列表
      let ret = []
      for (const p in this.drawDivObj) {
        ret = ret.concat(this.drawDivObj[p].divList || [])
      }
      return ret
    },
    rowWidthList() { // 每一行的宽度列表
      const ret = {}
      for (const top in this.positionMap) {
        let width = 0
        this.positionMap[top].forEach(item => {
          width += item.width
        })
        ret[top] = width
      }
      return ret
    }
  },
  mounted() {
    this.calcPosition()
  },
  methods: {
    calcPosition() {
      const wrapperEl = document.getElementsByClassName('word-wrapper')[0]
      const rowWidthTotal = wrapperEl.clientWidth // 父容器宽度
      let rowWidthNow = 0
      let rowTopNow = 0
      let rows = 0 // 文字行数
      let textHeight = 0 // 文字总高度
      let imgHeight = 0 // 图片总高度
      let paragraph // 图片分割开的每一段文字

      const reg = /\[插图\]/g
      let result = reg.exec(this.text)
      const toBeMatchedLength = 4
      let lastTextIndex = 0 // 上一段文字的起始索引位置
      let count = 0 // while循环的次数

      while (result !== null) {
        if (reg.lastIndex === 0) {
          paragraph = this.text.substring(0, result.index)
        } else {
          paragraph = this.text.substring(lastTextIndex, result.index)
        }
        lastTextIndex = result.index + toBeMatchedLength

        const textArr = paragraph.split('')
        const len = textArr.length

        for (let i = 0; i < len; i++) {
          const word = textArr[i]
          const spanEl = document.createElement('span')
          const textNode = document.createTextNode(word)
          spanEl.appendChild(textNode)
          spanEl.setAttribute('class', 'word')
          wrapperEl.appendChild(spanEl)
          const wordWidth = spanEl.clientWidth
          rowWidthNow += wordWidth
          if (!rowWidthNow || rowWidthNow < rowWidthTotal) {
            spanEl.setAttribute('style', `left:${rowWidthNow - wordWidth}px;top:${rowTopNow}px;`)
            spanEl.setAttribute('data-left', rowWidthNow - wordWidth)
            spanEl.setAttribute('data-top', rowTopNow)
            if (!this.positionMap[rowTopNow]) {
              rows++
              this.positionMap[rowTopNow] = []
            }
            this.positionMap[rowTopNow].push({
              left: rowWidthNow - wordWidth,
              width: wordWidth,
              word: word
            })
          } else {
            rowWidthNow = 0
            rowTopNow += parseInt(ROW_HEIGHT + ROW_GAP)
            spanEl.setAttribute('style', `left:${rowWidthNow}px;top:${rowTopNow}px;`)
            spanEl.setAttribute('data-left', rowWidthNow)
            spanEl.setAttribute('data-top', rowTopNow)
            if (!this.positionMap[rowTopNow]) {
              rows++
              this.positionMap[rowTopNow] = []
            }
            this.positionMap[rowTopNow].push({
              left: rowWidthNow,
              width: wordWidth,
              word: word
            })
            rowWidthNow += wordWidth
          }
          textHeight = (ROW_HEIGHT + ROW_GAP) * rows - ROW_GAP
        }

        const img = document.createElement('img')
        let top
        let width = this.imgList[count].width
        let height = this.imgList[count].height
        if (paragraph) {
          top = parseInt(rowTopNow + ROW_HEIGHT + IMG_GAP)
        } else { // 对于文章开头就是图片或者几个连续的图片不应该在加上行高
          top = parseInt(rowTopNow + IMG_GAP)
        }
        if (width > rowWidthTotal) {
          width = rowWidthTotal
          height = rowWidthTotal * (width / height)
        }
        img.setAttribute('src', this.imgList[count].url)
        img.setAttribute('class', 'image')
        img.setAttribute('style', `left:${(rowWidthTotal - width) / 2}px;
          width:${width}px;
          top:${top}px;
          height:${height}px`)
        img.addEventListener('touchstart', this.stopPropagation, false)
        wrapperEl.appendChild(img)
        this.positionMap[top] = [
          {
            left: (rowWidthTotal - width) / 2,
            width: width,
            top: top,
            height: height,
            word: '[插图]'
          }
        ]
        rowTopNow = parseInt(top + height + IMG_GAP)
        rowWidthNow = 0
        imgHeight += height + IMG_GAP * 2
        count++
        result = reg.exec(this.text)
      }
      paragraph = this.text.substring(lastTextIndex)

      if (paragraph) {
        rowWidthNow = 0
        const textArr = paragraph.split('')
        const len = textArr.length
        for (let i = 0; i < len; i++) {
          const word = textArr[i]
          const spanEl = document.createElement('span')
          const textNode = document.createTextNode(word)
          spanEl.appendChild(textNode)
          spanEl.setAttribute('class', 'word')
          wrapperEl.appendChild(spanEl)
          const wordWidth = spanEl.clientWidth
          rowWidthNow += wordWidth
          if (!rowWidthNow || rowWidthNow < rowWidthTotal) {
            spanEl.setAttribute('style', `left:${rowWidthNow - wordWidth}px;top:${rowTopNow}px;`)
            spanEl.setAttribute('data-left', rowWidthNow - wordWidth)
            spanEl.setAttribute('data-top', rowTopNow)
            if (!this.positionMap[rowTopNow]) {
              rows++
              this.positionMap[rowTopNow] = []
            }
            this.positionMap[rowTopNow].push({
              left: rowWidthNow - wordWidth,
              width: wordWidth,
              word: word
            })
          } else {
            rows++
            rowWidthNow = 0
            rowTopNow += ROW_HEIGHT + ROW_GAP
            spanEl.setAttribute('style', `left:${rowWidthNow}px;top:${rowTopNow}px;`)
            spanEl.setAttribute('data-left', rowWidthNow)
            spanEl.setAttribute('data-top', rowTopNow)
            if (!this.positionMap[rowTopNow]) this.positionMap[rowTopNow] = []
            this.positionMap[rowTopNow].push({
              left: rowWidthNow,
              width: wordWidth,
              word: word
            })
            rowWidthNow += wordWidth
          }
          textHeight = (ROW_HEIGHT + ROW_GAP) * rows - ROW_GAP
        }
      }
      wrapperEl.style.height = textHeight + imgHeight + 'px'
    },
    touchStart(e) {
      const self = this
      if (!this.cursorLeftPos && !this.cursorRightPos) {
        this.timeoutEvent = setTimeout(() => {
          self.createDynamicCursor(e)
        }, 700)
      }
    },
    touchMove() {
      if (this.timeoutEvent) {
        clearTimeout(this.timeoutEvent)
      }
    },
    touchEnd() {
      if (this.timeoutEvent) {
        clearTimeout(this.timeoutEvent)
      }
    },
    createDynamicCursor(e) {
      if (this.timeoutEvent) {
        clearTimeout(this.timeoutEvent)
      }

      if (e.target.className === 'word-wrapper') {
        return
      }

      if (this.currentParagraphKey) {
        this.currentParagraphKey = null
      }

      cancelable = false
      let width, left, top
      if (e.target.className.indexOf('underline') !== -1) { // 在已经划线的 div 上长按
        const x = parseInt(e.touches[0].clientX - 15) // 转换为相对于容器(即word-wrapper)的x轴坐标
        const pageYOffset = window.pageYOffset // 滚动条滚动偏移量
        const y = parseInt(e.touches[0].clientY + pageYOffset - 10) // 转换为相对于容器(即word-wrapper)的y轴坐标
        const tempArr = []
        for (const top in this.positionMap) {
          tempArr.push(parseInt(top))
        }
        const sortedTopList = this.sort(tempArr)
        const len = sortedTopList.length - 1

        for (let i = 0; i < len; i++) {
          if (y >= sortedTopList[i] && y < sortedTopList[i + 1]) {
            top = sortedTopList[i]
            break
          } else if (y >= sortedTopList[len]) {
            top = sortedTopList[len]
          }
        }

        const l = this.positionMap[top].length - 1
        for (let j = 0; j < l; j++) {
          const wordInfo = this.positionMap[top][j]
          const nextWordInfo = this.positionMap[top][j + 1]
          if (x >= wordInfo.left && x < nextWordInfo.left) {
            left = wordInfo.left
            width = wordInfo.width
          } else if (x >= this.positionMap[top][l].left &&
            x < this.positionMap[top][l].left + this.positionMap[top][l].width) {
            left = this.positionMap[top][l].left
            width = this.positionMap[top][l].width
          }
        }
      } else {
        const dataset = e.target.dataset
        left = parseInt(dataset.left)
        top = parseInt(dataset.top)
        width = parseInt(e.target.clientWidth)
      }

      this.$set(this.selectedDivList, 0, {
        left: left,
        top: top,
        width: width,
        height: ROW_HEIGHT
      })
      this.cursorLeftPos = {
        left: left - CURSOR_WIDTH,
        top: top + ROW_HEIGHT
      }
      this.cursorRightPos = {
        left: left + width,
        top: top + ROW_HEIGHT
      }
      this.joinTogether()
      this.calcToolBarPosition()
      setTimeout(() => {
        cancelable = true
      }, 200)
    },
    handleCursorLeftTouchMove(e) {
      this.isShowToolbar = false
      let finalLeft = 0
      let finalTop = 0
      const finalTopList = [] // 用户最终选中的top值列表
      const x = parseInt(e.touches[0].clientX - 15) // 转换为相对于容器(即word-wrapper)的x轴坐标
      const pageYOffset = window.pageYOffset // 滚动条滚动偏移量
      const y = parseInt(e.touches[0].clientY + pageYOffset - 10) // 转换为相对于容器(即word-wrapper)的y轴坐标
      const oWrapper = this.$refs['content-wrapper']
      const wrapperWidth = oWrapper.getBoundingClientRect().width
      const relativeTop = this.cursorRightPos.top - ROW_HEIGHT // 相对于右边游标的起始高度
      const relativeX = this.cursorRightPos.left // 相对于右边游标的起始x轴坐标
      if (y < relativeTop) { // 左边游标向上移动
        const tempArr = []
        for (const top in this.positionMap) {
          tempArr.push(parseInt(top))
        }
        const sortedTopList = this.sort(tempArr)
        const len = sortedTopList.length - 1

        for (let j = 0; j < len; j++) {
          if (y >= sortedTopList[j] && y < sortedTopList[j + 1]) {
            finalTop = sortedTopList[j]
            break
          }
        }

        sortedTopList.forEach(top => {
          if (top >= finalTop && top <= relativeTop) {
            finalTopList.push(top)
          }
        })
        this.selectedDivList = []
        const ftlLength = finalTopList.length
        for (let k = 0; k < ftlLength; k++) {
          const firstWordInfo = this.positionMap[finalTopList[k]][0]
          if (firstWordInfo.word === '[插图]') {
            this.$set(this.selectedDivList, k, {
              left: firstWordInfo.left,
              top: firstWordInfo.top,
              width: firstWordInfo.width,
              height: firstWordInfo.height
            })
            if (k === 0) {
              this.cursorLeftPos.left = firstWordInfo.left - CURSOR_WIDTH
              this.cursorLeftPos.top = firstWordInfo.top
            }
            continue
          }

          if (k === 0) {
            const l = this.positionMap[finalTop].length - 1
            for (let n = 0; n < l; n++) {
              const wordInfo = this.positionMap[finalTop][n]
              if (x >= wordInfo.left && x < this.positionMap[finalTop][n + 1].left) {
                finalLeft = wordInfo.left
                break
              } else if (x >= this.positionMap[finalTop][l].left && x < this.positionMap[finalTop][l].left + this.positionMap[finalTop][l].width) {
                finalLeft = this.positionMap[finalTop][l].left
              } else if (x >= this.positionMap[finalTop][l].left + this.positionMap[finalTop][l].width) {
                const rowWidth = this.rowWidthList[finalTopList[k]]
                const lastWordWidth = this.positionMap[finalTopList[k]][this.positionMap[finalTopList[k]].length - 1].width // 该行最后一个字的宽度
                if (wrapperWidth - rowWidth > lastWordWidth) { // 说明换行了，文字没有占满一行
                  finalLeft = rowWidth
                } else {
                  finalLeft = wrapperWidth
                }
              }
            }
            const rowWidth = this.rowWidthList[finalTopList[k]]
            const lastWordWidth = this.positionMap[finalTopList[k]][this.positionMap[finalTopList[k]].length - 1].width // 该行最后一个字的宽度
            if (wrapperWidth - rowWidth > lastWordWidth) { // 说明换行了，文字没有占满一行
              this.$set(this.selectedDivList, k, {
                left: finalLeft,
                top: finalTop,
                width: rowWidth - finalLeft,
                height: ROW_HEIGHT
              })
            } else {
              this.$set(this.selectedDivList, k, {
                left: finalLeft,
                top: finalTop,
                width: wrapperWidth - finalLeft,
                height: ROW_HEIGHT
              })
            }
            this.cursorLeftPos.left = finalLeft - CURSOR_WIDTH
            this.cursorLeftPos.top = finalTop + ROW_HEIGHT
          } else if (k === ftlLength - 1) {
            this.$set(this.selectedDivList, k, {
              left: 0,
              top: finalTopList[ftlLength - 1],
              width: relativeX,
              height: ROW_HEIGHT
            })
          } else {
            const rowWidth = this.rowWidthList[finalTopList[k]]
            const lastWordWidth = this.positionMap[finalTopList[k]][this.positionMap[finalTopList[k]].length - 1].width // 该行最后一个字的宽度
            if (wrapperWidth - rowWidth > lastWordWidth) { // 说明换行了，文字没有占满一行
              this.$set(this.selectedDivList, k, {
                left: 0,
                top: finalTopList[k],
                width: rowWidth,
                height: ROW_HEIGHT
              })
            } else {
              this.$set(this.selectedDivList, k, {
                left: 0,
                top: finalTopList[k],
                width: wrapperWidth,
                height: ROW_HEIGHT
              })
            }
          }
        }
      } else if (y >= relativeTop && y <= relativeTop + ROW_HEIGHT) { // 左边游标在选中行单方向移动
        if (x < relativeX && x >= 0) {
          this.selectedDivList = []
          const len = this.positionMap[relativeTop].length - 1
          for (let i = 0; i < len; i++) {
            const wordInfo = this.positionMap[relativeTop][i]
            const nextWordInfo = this.positionMap[relativeTop][i + 1]
            if (x >= wordInfo.left && x < nextWordInfo.left) {
              this.$set(this.selectedDivList, 0, {
                left: wordInfo.left,
                top: relativeTop,
                width: relativeX - wordInfo.left,
                height: ROW_HEIGHT
              })
              this.cursorLeftPos.left = wordInfo.left - CURSOR_WIDTH
              this.cursorLeftPos.top = relativeTop + ROW_HEIGHT
              break
            } else if ((x >= this.positionMap[relativeTop][len].left &&
              x < this.positionMap[relativeTop][len].left + this.positionMap[relativeTop][len].width) ||
              x >= this.positionMap[relativeTop][len].left + this.positionMap[relativeTop][len].width) {
              this.$set(this.selectedDivList, 0, {
                left: this.positionMap[relativeTop][len].left,
                top: relativeTop,
                width: relativeX - this.positionMap[relativeTop][len].left,
                height: ROW_HEIGHT
              })
              this.cursorLeftPos.left = this.positionMap[relativeTop][len].left - CURSOR_WIDTH
              this.cursorLeftPos.top = relativeTop + ROW_HEIGHT
            }
          }
        }
      }
    },
    handleCursorLeftTouchEnd() {
      this.joinTogether()
      this.calcToolBarPosition()
    },
    handleCursorRightTouchMove(e) {
      this.isShowToolbar = false
      let finalLeft = 0
      let finalTop = 0
      const finalTopList = [] // 用户最终选中的top值列表
      const x = parseInt(e.touches[0].clientX - 15) // 转换为相对于容器(即word-wrapper)的x轴坐标
      const pageYOffset = window.pageYOffset // 滚动条滚动偏移量
      const y = parseInt(e.touches[0].clientY + pageYOffset - 10) // 转换为相对于容器(即word-wrapper)的y轴坐标
      const oWrapper = this.$refs['content-wrapper']
      const wrapperWidth = oWrapper.getBoundingClientRect().width
      const relativeTop = this.cursorLeftPos.top - ROW_HEIGHT // // 相对于左边游标的起始高度
      const relativeX = this.cursorLeftPos.left + CURSOR_WIDTH // 相对于左边游标的起始x轴坐标

      if (y > relativeTop + ROW_HEIGHT + ROW_GAP) { // 右边游标向下移动
        const tempArr = []
        for (const top in this.positionMap) {
          tempArr.push(parseInt(top))
        }
        const sortedTopList = this.sort(tempArr)
        const len = sortedTopList.length - 1

        for (let j = 0; j < len; j++) {
          if (y >= sortedTopList[j] && y < sortedTopList[j + 1]) {
            finalTop = sortedTopList[j]
            break
          } else if (y >= sortedTopList[len]) {
            finalTop = sortedTopList[len]
          }
        }

        sortedTopList.forEach(top => {
          if (top >= relativeTop && top <= finalTop) {
            finalTopList.push(top)
          }
        })

        const ftlLength = finalTopList.length
        this.selectedDivList = []
        for (let k = 0; k < ftlLength; k++) {
          if (this.positionMap[finalTopList[k]][0].word === '[插图]') {
            this.$set(this.selectedDivList, k, {
              left: this.positionMap[finalTopList[k]][0].left,
              top: this.positionMap[finalTopList[k]][0].top,
              width: this.positionMap[finalTopList[k]][0].width,
              height: this.positionMap[finalTopList[k]][0].height
            })
            this.cursorRightPos.left = this.positionMap[finalTopList[k]][0].left + this.positionMap[finalTopList[k]][0].width
            this.cursorRightPos.top = this.positionMap[finalTopList[k]][0].top + this.positionMap[finalTopList[k]][0].height
            continue
          }

          if (k === 0) {
            const rowWidth = this.rowWidthList[finalTopList[k]]
            const lastWordWidth = this.positionMap[finalTopList[k]][this.positionMap[finalTopList[k]].length - 1].width // 该行最后一个字的宽度
            if (wrapperWidth - rowWidth > lastWordWidth) {
              this.$set(this.selectedDivList, k, {
                left: relativeX,
                top: relativeTop,
                width: rowWidth - relativeX,
                height: ROW_HEIGHT
              })
            } else {
              this.$set(this.selectedDivList, k, {
                left: relativeX,
                top: relativeTop,
                width: wrapperWidth - relativeX,
                height: ROW_HEIGHT
              })
            }
          } else if (k === ftlLength - 1) {
            const l = this.positionMap[finalTop].length - 1
            for (let n = 0; n < l; n++) {
              const wordInfo = this.positionMap[finalTop][n]
              if (x >= wordInfo.left && x < this.positionMap[finalTop][n + 1].left) {
                finalLeft = wordInfo.left
                break
              } else if (x >= this.positionMap[finalTop][l].left && x < this.positionMap[finalTop][l].left + this.positionMap[finalTop][l].width) {
                finalLeft = this.positionMap[finalTop][l].left
              } else if (x >= this.positionMap[finalTop][l].left + this.positionMap[finalTop][l].width) {
                finalLeft = this.positionMap[finalTop][l].left + this.positionMap[finalTop][l].width
              }
            }
            this.$set(this.selectedDivList, k, {
              left: 0,
              top: finalTop,
              width: finalLeft,
              height: ROW_HEIGHT
            })
            this.cursorRightPos.left = finalLeft
            this.cursorRightPos.top = finalTop + ROW_HEIGHT
          } else {
            const rowWidth = this.rowWidthList[finalTopList[k]]
            const lastWordWidth = this.positionMap[finalTopList[k]][this.positionMap[finalTopList[k]].length - 1].width // 该行最后一个字的宽度
            if (wrapperWidth - rowWidth > lastWordWidth) {
              this.$set(this.selectedDivList, k, {
                left: 0,
                top: finalTopList[k],
                width: rowWidth,
                height: ROW_HEIGHT
              })
            } else {
              this.$set(this.selectedDivList, k, {
                left: 0,
                top: finalTopList[k],
                width: wrapperWidth,
                height: ROW_HEIGHT
              })
            }
          }
        }
      } else if (y >= relativeTop && y <= relativeTop + ROW_HEIGHT) { // 右边游标在选中行单方向移动
        if (x > relativeX) {
          const len = this.positionMap[relativeTop].length - 1
          this.selectedDivList = []
          for (let i = 0; i < len; i++) {
            const wordInfo = this.positionMap[relativeTop][i]
            const nextWordInfo = this.positionMap[relativeTop][i + 1]
            if (x >= wordInfo.left && x < nextWordInfo.left) {
              this.$set(this.selectedDivList, 0, {
                left: relativeX,
                top: relativeTop,
                width: wordInfo.left - relativeX === 0 ? wordInfo.width : wordInfo.left - relativeX,
                height: ROW_HEIGHT
              })
              this.cursorRightPos.left = wordInfo.left === relativeX ? wordInfo.left + wordInfo.width : wordInfo.left
              this.cursorRightPos.top = relativeTop + ROW_HEIGHT
              break
            } else if (x >= this.positionMap[relativeTop][len].left && x < this.positionMap[relativeTop][len].left + this.positionMap[relativeTop][len].width) {
              this.$set(this.selectedDivList, 0, {
                left: relativeX,
                top: relativeTop,
                width: this.positionMap[relativeTop][len].left - relativeX,
                height: ROW_HEIGHT
              })
              this.cursorRightPos.left = this.positionMap[relativeTop][len].left
              this.cursorRightPos.top = relativeTop + ROW_HEIGHT
            } else if (x >= this.positionMap[relativeTop][len].left + this.positionMap[relativeTop][len].width) {
              const rowWidth = this.rowWidthList[relativeTop]
              const lastWordWidth = this.positionMap[relativeTop][this.positionMap[relativeTop].length - 1].width // 该行最后一个字的宽度
              if (wrapperWidth - rowWidth > lastWordWidth) {
                this.$set(this.selectedDivList, 0, {
                  left: relativeX,
                  top: relativeTop,
                  width: rowWidth - relativeX,
                  height: ROW_HEIGHT
                })
                this.cursorRightPos.left = rowWidth
              } else {
                this.$set(this.selectedDivList, 0, {
                  left: relativeX,
                  top: relativeTop,
                  width: wrapperWidth - relativeX,
                  height: ROW_HEIGHT
                })
                this.cursorRightPos.left = wrapperWidth
              }
              this.cursorRightPos.top = relativeTop + ROW_HEIGHT
            }
          }
        }
      }
    },
    handleCursorRightTouchEnd() {
      this.joinTogether()
      this.calcToolBarPosition()
    },
    calcToolBarPosition(key) {
      const pageYOffset = window.pageYOffset // 滚动条y轴偏移量
      const oWrapper = this.$refs['content-wrapper']
      const wrapperWidth = oWrapper.getBoundingClientRect().width
      const GAP = 10 // 工具栏距离选中行间隔
      const TOOLBAR_HEIGHT = 50 // 工具栏高度
      // const TOOLBAR_WIDTH = 70 // 工具栏宽度

      let firstSelectedRowPosInfo
      if (key) {
        if (this.selectedDivList.length) {
          this.cursorLeftPos = null
          this.cursorRightPos = null
          this.selectedDivList = []
        }

        this.currentParagraphKey = key
        this.isShowToolbar = false
        firstSelectedRowPosInfo = this.drawDivObj[key].divList[0]
      } else {
        firstSelectedRowPosInfo = this.selectedDivList[0]
      }

      const TOOLBAR_WIDTH = this.currentParagraphKey ? 215 : 158

      if (firstSelectedRowPosInfo.top - pageYOffset > TOOLBAR_HEIGHT) {
        this.toolbarPos.top = firstSelectedRowPosInfo.top - GAP - TOOLBAR_HEIGHT
        if (firstSelectedRowPosInfo.width >= TOOLBAR_WIDTH) {
          this.toolbarPos.left = (firstSelectedRowPosInfo.width - TOOLBAR_WIDTH) / 2 + firstSelectedRowPosInfo.left
        } else if (firstSelectedRowPosInfo.left >= (TOOLBAR_WIDTH - firstSelectedRowPosInfo.width) / 2 &&
          wrapperWidth - firstSelectedRowPosInfo.left - firstSelectedRowPosInfo.width >= (TOOLBAR_WIDTH - firstSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = firstSelectedRowPosInfo.left - (TOOLBAR_WIDTH - firstSelectedRowPosInfo.width) / 2
        } else if (firstSelectedRowPosInfo.left < (TOOLBAR_WIDTH - firstSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = 0
        } else if (wrapperWidth - firstSelectedRowPosInfo.left - firstSelectedRowPosInfo.width < (TOOLBAR_WIDTH - firstSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = wrapperWidth - TOOLBAR_WIDTH
        }
      } else {
        let lastSelectedRowPosInfo
        if (key) {
          lastSelectedRowPosInfo = this.drawDivObj[key].divList[this.drawDivObj[key].divList.length - 1]
        } else {
          lastSelectedRowPosInfo = this.selectedDivList[this.selectedDivList.length - 1]
        }
        this.toolbarPos.top = lastSelectedRowPosInfo.top + GAP + ROW_HEIGHT
        if (lastSelectedRowPosInfo.width >= TOOLBAR_WIDTH) {
          this.toolbarPos.left = (lastSelectedRowPosInfo.width - TOOLBAR_WIDTH) / 2 + lastSelectedRowPosInfo.left
        } else if (lastSelectedRowPosInfo.left >= (TOOLBAR_WIDTH - lastSelectedRowPosInfo.width) / 2 &&
          wrapperWidth - lastSelectedRowPosInfo.left - lastSelectedRowPosInfo.width >= (TOOLBAR_WIDTH - lastSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = lastSelectedRowPosInfo.left - (TOOLBAR_WIDTH - lastSelectedRowPosInfo.width) / 2
        } else if (lastSelectedRowPosInfo.left < (TOOLBAR_WIDTH - lastSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = 0
        } else if (wrapperWidth - lastSelectedRowPosInfo.left - lastSelectedRowPosInfo.width < (TOOLBAR_WIDTH - lastSelectedRowPosInfo.width) / 2) {
          this.toolbarPos.left = wrapperWidth - TOOLBAR_WIDTH
        }
      }
      this.isShowToolbar = true
    },
    copyText() {
      let value = ''
      if (this.currentParagraphKey) {
        value = this.drawDivObj[this.currentParagraphKey].text
      } else {
        value = this.selectedText
      }
      const textArea = document.createElement('textarea')
      textArea.style.position = 'fixed'
      textArea.style.top = '0'
      textArea.style.left = '0'
      textArea.style.width = '2em'
      textArea.style.height = '2em'
      textArea.style.padding = '0'
      textArea.style.border = 'none'
      textArea.style.outline = 'none'
      textArea.style.boxShadow = 'none'
      textArea.style.background = 'transparent'
      textArea.value = value
      document.body.appendChild(textArea)
      textArea.select()

      try {
        const successful = document.execCommand('copy')
        const msg = successful ? '成功复制到剪贴板' : '该浏览器不支持点击复制到剪贴板'
        alert(msg)
      } catch (err) {
        alert('该浏览器不支持点击复制到剪贴板')
      }

      this.cancelSelect()
      document.body.removeChild(textArea)
    },
    drawLine(lineType) {
      if (this.currentParagraphKey && lineType !== this.drawDivObj[this.currentParagraphKey].divList[0].class) {
        this.drawDivObj[this.currentParagraphKey].divList.forEach(divItem => {
          divItem.class = lineType
          if (lineType === 'mark' && !divItem.bg) {
            divItem.bg = Math.random() > 0.5 ? 'bg1' : 'bg2'
          }
        })
        return
      }

      const key = Date.now()
      const bg = lineType === 'mark' ? (Math.random() > 0.5 ? 'bg1' : 'bg2') : null
      const toBeReplacedDivArr = [] // 待替换的 重复部分的 div列表
      const repeatKeys = new Set()
      let splicedArr = [] // 拼接的 所有段落的div
      const splicedTopList = [] // 拼接的 所有段落的 top 列表
      let divList // 最终要划线的列表
      this.selectedDivList.forEach(divItem => { // 判断划线 是否有 和之前已经画过的线 有重复(交集)
        for (const key in this.drawDivObj) {
          this.drawDivObj[key].divList && this.drawDivObj[key].divList.forEach(paintedDivItem => {
            if (divItem.top === paintedDivItem.top) {
              if (!(divItem.left + divItem.width < paintedDivItem.left || paintedDivItem.left + paintedDivItem.width < divItem.left)) {
                // 相交区域
                repeatKeys.add(key)

                let width
                if (divItem.left === paintedDivItem.left) { // 如果 left 值相等 哪个宽度宽 宽度就用哪个 否则 两个宽度相加减掉重复相交的宽度作为最终宽度
                  width = divItem.width >= paintedDivItem.width ? divItem.width : paintedDivItem.width
                } else if (divItem.left < paintedDivItem.left) {
                  width = paintedDivItem.width + divItem.width - (divItem.left + divItem.width - paintedDivItem.left)
                } else {
                  width = paintedDivItem.width + divItem.width - (paintedDivItem.left + paintedDivItem.width - divItem.left)
                }

                toBeReplacedDivArr.push({
                  left: divItem.left <= paintedDivItem.left ? divItem.left : paintedDivItem.left,
                  top: paintedDivItem.top,
                  width: width,
                  height: paintedDivItem.height
                })
              }
            }
          })
        }
      })
      if (repeatKeys.size) {
        // 拼接所有段落
        repeatKeys.forEach(key => {
          splicedArr = splicedArr.concat(this.drawDivObj[key].divList)
          this.$set(this.drawDivObj, key, {})
        })

        // 替换掉划线重复的 div
        splicedArr.forEach(divItem => {
          toBeReplacedDivArr.forEach(repdDivItem => {
            if (divItem.top === repdDivItem.top) {
              divItem.left = repdDivItem.left
              divItem.width = repdDivItem.width
              divItem.height = repdDivItem.height
            }
          })
          splicedTopList.push(divItem.top)
        })

        // 补充数组中没有的 top 值对应的 div
        this.selectedDivList.forEach(divItem => {
          if (splicedTopList.indexOf(divItem.top) === -1) {
            splicedArr.push(divItem)
          }
        })

        // 按 top 值从小到大顺序排序
        splicedArr = splicedArr.sort((a, b) => {
          return a.top - b.top
        })

        divList = splicedArr.map(item => {
          return {
            left: item.left,
            top: item.top,
            width: item.width,
            class: lineType,
            key: key,
            fun: this.calcToolBarPosition,
            bg,
            height: item.height
          }
        })
      } else {
        divList = this.selectedDivList.map(item => {
          return {
            left: item.left,
            top: item.top,
            width: item.width,
            class: lineType,
            key: key,
            fun: this.calcToolBarPosition,
            bg,
            height: item.height
          }
        })
      }

      this.$set(this.drawDivObj, key, {
        divList
      })
      if (repeatKeys.size) {
        this.drawDivObj[key].text = this.joinTogether(splicedArr)
      } else {
        this.drawDivObj[key].text = this.selectedText
      }

      this.cancelSelect()
    },
    delLine() {
      this.$set(this.drawDivObj, this.currentParagraphKey, {})
    },
    cancelSelect() {
      if (cancelable) {
        this.cursorLeftPos = null
        this.cursorRightPos = null
        this.selectedDivList = []
        this.isShowToolbar = false
      }
      this.selectedText = ''
    },
    sort(arr) {
      return arr.sort((a, b) => a - b)
    },
    joinTogether(arr) {
      let text = ''
      const tempArr = arr || this.selectedDivList
      const selectedDivListLength = tempArr.length
      if (selectedDivListLength === 1) { // 只选中了一行
        this.positionMap[tempArr[0].top].forEach(wordItem => {
          if (wordItem.left >= tempArr[0].left && wordItem.left < tempArr[0].left + tempArr[0].width) {
            text += wordItem.word
          }
        })
      } else if (selectedDivListLength > 1) {
        tempArr.forEach((selectedRowItem, index) => {
          if (index === 0) {
            this.positionMap[selectedRowItem.top].forEach(wordItem => {
              if (wordItem.left >= selectedRowItem.left) {
                text += wordItem.word
              }
            })
          } else if (index === selectedDivListLength - 1) {
            this.positionMap[selectedRowItem.top].forEach(wordItem => {
              if (wordItem.left < selectedRowItem.width) {
                text += wordItem.word
              }
            })
          } else {
            this.positionMap[selectedRowItem.top].forEach(wordItem => {
              text += wordItem.word
            })
          }
        })
      }
      if (arr) {
        return text.trim().replace(/(\r\n|\r|\n|\s)/g, '')
      } else {
        this.selectedText = text.trim().replace(/(\r\n|\r|\n|\s)/g, '')
      }
    },
    preventContextMenu(e) {
      e.preventDefault()
    },
    stopPropagation(e) {
      e.stopPropagation()
    }
  }
}
</script>
<style lang="less">
.home {
  padding: 10px 15px;
  .hidden {
    position: fixed;
    z-index: -999;
    visibility: hidden;
  }
  .word-wrapper {
    position: relative;
    .word {
      position: absolute;
      line-height: 23px;
    }
    .image {
      position: absolute;
    }
    .cursor-left, .cursor-right {
      position: absolute;
      width: 20px;
      height: 20px;
      background: rgba(45, 233, 7, 0.6);
      z-index: 6;
      touch-action: none;
    }
    .selection {
      position: absolute;
      z-index: 3;
      background: rgba(0, 151, 255, .15);
    }
    .underline {
      position: absolute;
      height: 23px;
      padding-bottom: 2px;
      z-index: 3;
      background-position: 0 100%;
      background-repeat: no-repeat;
      background-size: 100% 3px;
      &.s0 {
        background-image: url('../assets/underline.png');
      }
      &.s2 {
        background-size: 9px 4px;
        background-repeat: repeat-x;
        background-image: url('../assets/breakline.png');
      }
      &.s1 {
        box-sizing: border-box;
        padding-left: 14px;
        padding-right: 14px;
        background-image: url('../assets/mark_bg.png');
        background-size: 100% 100%;
        background-clip: content-box;
        background-repeat: repeat-x;
        opacity: 0.15;
        &::before,
        &::after {
          position: absolute;
          width: 14px;
          height: 100%;
          content: '';
          background-size: 100% 100%;
        }
        &.bg1::before {
          left: 0;
          background-image: url('../assets/mark_before1.png');
        }
        &.bg1::after {
          right: 0;
          background-image: url('../assets/mark_after1.png');
        }
        &.bg2::before {
          left: 0;
          background-image: url('../assets/mark_before2.png');
        }
        &.bg2::after {
          right: 0;
          background-image: url('../assets/mark_after2.png');
        }
      }
    }
    .toolbar-wrapper {
      position: absolute;
      padding: 0 4px;
      height: 50px;
      z-index: 5;
      background-color: #3a3c3e;
      .btn {
        margin-top: 12px;
      }
    }
  }
}
</style>
